@import url(https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;300;400;500;600;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;600;700;800;900&display=swap);

* {
  box-sizing: border-box;
  padding: 0%;
  margin: 0%;
  font-family: 'Outfit', sans-serif;
}

body {
  background-color: black;
}

h1, h4, p, 
a {
  color: white;
  text-decoration: none;
}

ul {
  list-style-type: none;
}

.btn {
  padding: 12px 32px;
  font-size: 1rem;
  text-transform: uppercase;
  background: rgba(0, 255, 0, 0.951);
  color: #222;
  border: 1px solid #fff;
  font-weight: 600;
  cursor: pointer;
}

.btn-light {
  background: transparent;
  color: #fff;
}

.btn:hover {
  background: rgba(255, 255, 255, 0.2);
  color: #fff;
  transition: 0.3s;
}


.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 10;
  }
  
  .header-bg {
      background-color: rgb(0, 0, 0, 0.85);
      transition: 0.5s;
  }
  
  .nav-menu {
    display: flex;
   
  }
  
  .nav-menu li {
    padding: 0 1rem;
  }
  
  .nav-menu li a {
    font-size: 1.2rem;
    font-weight: 500;
  }
  
  .hamburger {
      display: none;
  }
  
  
  @media screen and (max-width: 1040px) {
      .nav-menu {
          flex-direction: column;
          justify-content: center;
          align-items: center;
          width: 100%;
          height: 100vh;
          background: rgba(0, 0, 0, 0.9);
          position: absolute;
          top: 0;
          left: -100%;
          z-index: -3;
          transition: 0.3s;
      }
  
      .nav-menu.active {
          left: 0;
      }
  
      .nav-menu li {
          padding: 1rem 0;
      }
  
  .nav-menu li a {
      font-size: 2rem;
  }
  
  .hamburger {
      display: initial;
  }
  
  }
.mask {
    width: 100%;
    height: 100vh;
    position: relative;
}

.hero-img {
 width: 100%;
 height: 100%;
 background-size: cover;   
 background-position: center;
 object-fit: cover;
}

.mask::after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: black;
    opacity: 0.8;
}

.hero {
    height: 100%;
    width: 100%;
}

.hero .content {
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    left: 50%;
    text-align: center;
}

.hero .content h1 {
    font-size: 4rem;
    padding: 0.6rem 0 1.5rem;
}

.hero .content p {
    font-size: 1.4rem;
    font-weight: 200;
   text-transform: uppercase;
}

.content .btn {
   margin: 1rem 0.2rem
}

@media screen and (max-width: 640px) {
    
    .hero .content h1 {
        font-size: 3rem;
      
    }
    
    .hero .content p {
        font-size: 1.4rem;
 
    }
}




.project-heading {
    text-align: center;
    padding: 4rem 0 2rem 0;
    color: white;
  }
  
  .project-container {
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 40px;
    gap: 40px;
    padding-bottom: 5rem;
    justify-content: center;
  }
  
  .project-card {
    background: #1a1919;
    padding: 1.2rem 1rem;
  }
  
  .project-card img {
    width: 100%;
    height: 15rem;

  }
  
  .project-Title {
    color: white;
    padding: 1rem 0;
  }
  
  .pro-details p {
    padding-bottom: 1rem;
    font-size: 1.1rem;
    text-align: justify;
  }
  
  .pro-btns {
    display: flex;
    justify-content: space-between;
    padding: 0.5rem 0;
  }
  
  .pro-btns .btn {
    padding: 0.5rem 1rem;
  }
  
  @media screen and (max-width: 740px) {
    .project-container {
      max-width: 90%;
      margin: auto;
      grid-template-columns: 1fr;
    }
  }
  
.footer {
    width: 100%;
    padding: 6rem 0;
    background-color: rgba(19, 19, 19, 0.8);
}

.footer-container {
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2, 1f);
    grid-gap: 40px;
    gap: 40px;
}

.left {
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.location {
    display: flex;
    margin-bottom: 0.8rem;
}

.location p {
    line-height: 30px;
}

.right {
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.social {
    margin-top: 1rem;
}

h4 {
    font-size: 1.2rem;
    padding-bottom: 0.7rem;
}

p {
    font-size: 1.2rem;
}

@media screen and (max-width: 640px){
    
    .footer-container {
      
        grid-template-columns: 1fr;
      
    }
}
/* Importing google font  */

.text-shpere {
    position: relative;
    top: 0;
    width: 100%;
    height: 600px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: black;
}

.tagcloud {
    display: inline-block;
    top: 0;
    left: 0;
    font-family: 'Inter', sans-serif;
    font-weight: 600;
    letter-spacing: 0.0625em;
    font-size: 1.4em;
}

/* Change color of each text in sphere on hover   */
.tagcloud--item {
    color: rgb(0, 255, 0);
    text-transform: uppercase;
}

.tagcloud--item:hover {
    color: white;
}
